
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Popup from 'vux/src/components/popup/index.vue'

import { msgFormatter } from '../utils'

export default {
name: 'Home',
data () {
return {
user: {
username: '',
password: ''
},
valid: false,
loading: false,
error: '',
illegalTriedLogin: false,
illegalTrial: false
}
},
methods: {
validate () {
let valid = true
for (let x in this.$refs) {
valid = this.$refs[x].valid && valid
}
this.valid = valid
},
submit () {
if (this.loading) {
return
}
this.loading = true
if (this.valid) {
this.$store.dispatch('login', {
user: this.user
}).then(res => {
return this.$store.dispatch('fetchUser')
}).then(res => {
this.illegalTriedLogin = false
this.error = ''
this.loading = false
this.$router.push('/chatroom')
}).catch((error) => {
this.error = msgFormatter(error)
this.loading = false
})
}
}
},
watch: {
'error': function (error) {
if (error) {
setTimeout(() => {
this.error = ''
}, 3000)
}
}
},
components: {
XInput,
Group,
XButton,
Flexbox,
FlexboxItem,
Popup
}
}



